@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  font-family: Evanses;
  background: rgba(244, 244, 245, 1);
}

@layer base {
  @font-face {
    font-family: Evanses;
    font-weight: 400;
    src: url(assets/fonts/Poppins-Regular.ttf);
    font-display: swap;
  }
}
.image-gallery-thumbnail {
  height: 55px;
  width: 55px !important;
}
.image-gallery-thumbnail.active {
  border: 0.2px solid #c5c3c3 !important;
}
